import React, {Component} from 'react';
import './App.css'

let question1 = prompt("What does D.C. stand for? (Washington D.C.)");
let question2 = prompt("What continent is Russia apart of?");
let question3 = prompt("Why couldn't the toilet paper cross the road?");
let answer1 = "District Columbia";
let answer2 = "Europe";
let answer3 = "Because it was afraid of getting stuck in the crack";

class Quiz extends Component{
    constructor(){
        super()
        this.state = {
            quiz: "Your Answer: " + question1,
            quiz2: "Your Answer: " + question2,
            quiz3: "Your Answer: " + question3
        }
    }

    revealAnswer(){
        this.setState({
            quiz: answer1 + ", is the right answer"
        });
    }

    revealAnswer2(){
        this.setState({
            quiz2: answer2 + ", is the right answer"
        });
    }

    revealAnswer3(){
        this.setState({
            quiz3: answer3 + ", is the right answer"
        });
    }

    render(){
        return(
            <div>
            <h1>Quiz Time!</h1>
                <div>
                    <h3>"What does D.C. stand for? (Washington D.C.)"</h3>
                    <p>{this.state.quiz}</p>
                    <button class="btn btn-light" onClick={() => this.revealAnswer()}>Reveal Answer</button>
                </div><br/>
                    
                    <div>
                        <h3>"What continent is Russia apart of?"</h3>
                        <p>{this.state.quiz2}</p>
                        <button class="btn btn-light" onClick={() => this.revealAnswer2()}>Reveal Answer</button>  
                    </div><br/>

                    <div>
                        <h3>"Why couldn't the toilet paper cross the road?"</h3>
                        <p>{this.state.quiz3}</p>
                        <button class="btn btn-light" onClick={() => this.revealAnswer3()}>Reveal Answer</button>  
                    </div>
            </div>
        )
    }
}

export default Quiz;